  justify-content: center;
  flex-direction: column; }

.item-container {
  position: relative;
  display: flex;
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px;
  color: #317BE6;
  text-decoration: none; }
  .item-container.selected {
    color: #888888;
    cursor: default; }

